import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { Button } from 'semantic-ui-react';
import Amount from '../../Amount';
import { stateToForm, AccountGroupT } from '../../../entities/Account';

class AccountItem extends React.Component {
  handleEditClick = () => {
    this.props.openAccountInModal(stateToForm(this.props.account));
  };

  render() {
    const { account } = this.props;
    return (
      <div className="account-widget-account">
        <div className="account-widget-account__name">
          <Link to={`/transactions/${account.id}`}>
            {account.name}
          </Link>
        </div>
        <div className="account-widget-account__balance">
          {account.currencies.map(code => (
            <React.Fragment>
              <Amount
                key={code}
                value={account.balance[code]}
                code={code}
              />
              {(this.props.group === AccountGroupT.Asset) &&
                <React.Fragment>
                  <Amount
                    key={code + '-debt'}
                    value={-account.debt[code]}
                    code={code}
                  />
                  <Amount
                    key={code}
                    value={account.balance[code] - account.debt[code]}
                    code={code}
                  />
                </React.Fragment>
              }
            </React.Fragment>
          ))}
        </div>
        {this.props.openAccountInModal && (
          <div className="account-widget-account__edit">
            <Button
              basic
              circular
              icon="pencil"
              onClick={this.handleEditClick}
            />
          </div>
        )}
      </div>
    );
  }
}

AccountItem.propTypes = {
  account: PropTypes.shape({
    name: PropTypes.string.isRequired,
    balance: PropTypes.objectOf(PropTypes.number).isRequired
  }).isRequired,
  openAccountInModal: PropTypes.func
};

export default AccountItem;
